<template>
  <div class="a-btn" :class="btnClass" @click="handleClick">
    <div class="btn-icon" :class="'fa fa-' + icon"></div>
    <div class="btn-name">
      <slot></slot>
    </div>
  </div>
</template>

<script>
const COMPONENT_NAME = 'cube-button'
export default {
  name: COMPONENT_NAME,
  props: {
    icon: {
      type: String,
      default: ''
    },
    active: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    inline: {
      type: Boolean,
      default: false
    },
    primary: {
      type: Boolean,
      default: false
    },
    outline: {
      type: Boolean,
      default: false
    },
    light: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: 'button'
    }
  },
  computed: {
    btnClass() {
      return {
        'cube-btn_active': this.active,
        'cube-btn_disabled': this.disabled,
        'cube-btn-inline': this.inline,
        'cube-btn-primary': this.primary,
        'cube-btn-outline': this.outline,
        'cube-btn-outline-primary': this.outline && this.primary,
        'cube-btn-light': this.light
      }
    }
  },
  methods: {
    handleClick(event) {
      if (this.disabled) {
        event.preventDefault()
        event.stopPropagation()
        return
      }
      this.$emit('click', event)
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @require "~common/stylus/variable.styl"
  @require "~common/stylus/mixin.styl"
  btn-active($bg, $border = $bg)
  &.cube-btn_active, &:active
    background: $bg
    border-1px($border)

  .a-btn
    display: block
    margin: 0
    padding: 10px 16px
    width: 100%
    text-align: center
    white-space: nowrap
    cursor: pointer
    font-size: $fontsize-large
    line-height: 1
    color: $btn-primary-color
    outline: none
    border: none
    box-sizing: border-box
    -webkit-tap-highlight-color: transparent
    .btn-icon
      font-size: 100%
      transform: scale(1.13)
      //transform-origin: right center
    .btn-name
      font-size: $fontsize-small
      margin: 5px
    btn-active($btn-active-bgc, $btn-active-bdc)
    &.cube-btn_disabled,
    &.cube-btn_disabled.cube-btn_active,
    &.cube-btn_disabled:active
      color: $btn-disabled-color
      background: $btn-disabled-bgc
      border-1px($btn-disabled-bdc)

  .cube-btn-inline
    width: auto
    display: inline-block
    vertical-align: middle
    padding: 9px 10px
    font-size: $fontsize-small
    > i
      margin-right: 2px
      transform: scale(1.14)

  .cube-btn-primary
    color: $btn-primary-color
    background: $btn-primary-bgc
    border-1px($btn-primary-bdc)
    btn-active($btn-primary-active-bgc, $btn-primary-active-bdc)

  .cube-btn-light
    color: $btn-light-color
    background: $btn-light-bgc
    box-shadow: $box-shadow-content
    btn-active($btn-light-active-bgc, $btn-light-active-bdc)

  .cube-btn-outline
    color: $btn-outline-color
    background: $btn-outline-bgc
    border-1px($btn-outline-bdc)
    btn-active($btn-outline-active-bgc, $btn-outline-active-bdc)

  .cube-btn-outline-primary
    color: $btn-outline-primary-color
    background: $btn-outline-primary-bgc
    border-1px($btn-outline-primary-bdc)
    btn-active($btn-outline-primary-active-bgc, $btn-outline-primary-active-bdc)
</style>



